* {
    margin: 0;
    padding: 0;
}

@mixin btns() {
    width: 200px;
    color: white;
    height: 40px;
    border: 0;
    display: block;
    font-size: 20px;
    border-radius: 20px;
    background-color: #60D;
    box-shadow: 0px 0px 10px #60D
}

.container {
    height: 100vh;
    display: flex;
    align-items: center;
    font-family: sans-serif;
    justify-content: center;
    background-color: #60D;
}
.main-block {
    width: 600px;
    height: 600px;
    display: flex;
    border-radius: 50px;
    justify-content: center;
    background-color: #fff;
    
}
.margin-line {
    width: 351px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    
}
.name {
    font-size: 50px;
    margin-top: 20px;
}
.description {
    font-size: 23px;
     
}
.buttons {
    display: flex;
    flex-wrap: wrap;
    align-self: flex-end;     
    margin-bottom: 20px;
    justify-content: center;
    width: 200px;

}
.about {
    @include btns();
   margin-bottom: 20px;
}
.projects {
    @include btns();
}
.contact {
    @include btns();
    margin-top: 20px;
}